<template>
  <div v-if="options.length" class="filter">
    <FilterHead :is-plus="isBigAccordHide" :title="title" @toggleView="toggleBigAccord"/>
    <div ref="bigAccord" :style="{ height: bigAccordHeight }" class="filter__accordion">
      <input v-if="withSearch" :name="filterKey" autocomplete="off" class="filter__search"
             placeholder="Быстрый поиск" type="text">
      <!--  Список опций с чекбоксами    -->
      <ul v-if="isCheckboxModel" ref="smallAccord" :style="{ height: smallAccordHeight }"
          class="filter__list">
        <li v-for="option in options" class="filter__item filter__item--thin">
          <button class="filter__option" type="button"
                  @click="optionsClickHandler(filterKey, option.value, $event)">
            <span class="filter__checkbox">
              <svg :class="{'filter__check--checked': 0 <= checkList.indexOf(option.value)}"
                   class="filter__check"
                   height="8" width="10" xmlns="http://www.w3.org/2000/svg">
                <path d="M9 1L4.1 6 1 2.85" stroke="#4B4B4B" stroke-miterlimit="10"
                      stroke-width="2"/>
              </svg>
            </span>
            {{ option.value }}
          </button>
        </li>
      </ul>
      <!--  Версия списка опций без чекбоксов    -->
      <ul v-else ref="smallAccord" :style="{ height: smallAccordHeight }" class="filter__list">
        <li v-for="option in options" class="filter__item">
          <button class="filter__option" type="button"
                  @click="optionsClickHandler(filterKey, option.value, $event)">
            <span :class="{'filter__clear--checked': 0 <= checkList.indexOf(option.value)}"
                  class="filter__clear"></span>
            {{ option.value }}
          </button>
          <span class="filter__number">{{ option.count }}</span>
        </li>
      </ul>
      <!--      -->
      <button v-if="options.length > itemsNumber" class="filter__footer" type="button"
              @click="toggleSmallAccord">
        <span :class="{ 'filter__mark--plus': isSmallAccordHide }"
              class="filter__mark filter__mark--footer">
        </span><span v-show="isSmallAccordHide">{{ viewToggleCapture }}</span><span
          v-show="!isSmallAccordHide">Свернуть</span>
      </button>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from "vuex";
import FilterHead from "./Filter-head";

// Колличество элементв в сокращенном списке опций для фильтра
const ITEMS_LIMIT = 7;

export default {
  name: "FilterUnit",
  components: {FilterHead},
  props: {
    // Массив опций фильтра
    allOptions: Object,
    // Массив опций в состоянии «checked»
    checkedOptions: Object,
    // Флаг стиля отображения фильтра (с чекбоксами true без false)
    isCheckboxModel: Boolean,
    // Флаг отображения поля «быстрый поиск» (true – отображать)
    withSearch: Boolean,
    // Имя ключа по значению которого будет производиться фильтраця
    filterKey: String,
    // Наименование блока отображаемое на экране
    title: String,
    // Текстовой контент кнопки по клику на которую разворачивается полный список опций
    viewToggleCapture: String
  },
  data: () => {
    return {
      // Состояние блока, true свернут
      isBigAccordHide: false,
      // Состояние списка опций, false ограничен определенным числом опчий
      isSmallAccordHide: true,
      // Значение height элемента .filter__accordion
      bigAccordHeight: 'auto',
      // Значение height элемента .filter__list
      smallAccordHeight: 'auto',
      // Минимальная высота элемента .filter__list
      smallAccordMinHeight: 0,
      // Колличество опций отображаемых на экране при значении isSmallAccordHide = false
      itemsNumber: ITEMS_LIMIT,
      // Флаг необходимасти произвести инициализацию переменных в акордеонах
      isInit: true
    }
  },
  methods: {
    ...mapActions(['toggleOptions']),
    // Метод сворачивает и разворачивает внешний аккордион
    toggleBigAccord() {
      this.isBigAccordHide = !this.isBigAccordHide;
      if (this.bigAccordHeight === `auto` || !this.isBigAccordHide) {
        this.bigAccordHeight = `${this.$refs.bigAccord.scrollHeight}px`;
      } else {
        this.bigAccordHeight = `0`;
      }
    },
    // Метод переключает между ограниченным и полным колличеством видимых опций фильтра
    toggleSmallAccord() {
      this.isSmallAccordHide = !this.isSmallAccordHide;
      this.bigAccordHeight = `auto`;
      this.smallAccordHeight = this.isSmallAccordHide
          ? `${this.smallAccordMinHeight}px`
          : `${this.$refs.smallAccord.scrollHeight}px`
    },
    // Метод инициализирует стартовые значения высот элементов
    init() {
      if (this.options.length > this.itemsNumber) {
        this.smallAccordMinHeight = this.$refs.smallAccord.childNodes[this.itemsNumber].offsetTop
            - this.$refs.smallAccord.childNodes[0].offsetTop;
        this.smallAccordHeight = `${this.smallAccordMinHeight}px`;
      } else {
        this.smallAccordHeight = `auto`;
      }
      this.bigAccordHeight = `auto`;
      this.isSmallAccordHide = true;
      this.isBigAccordHide = false;
    },
    optionsClickHandler(key, value) {
      this.toggleOptions([key, value]);
    }
  },
  computed: {
    ...mapGetters(['getAllOptions', 'getCheckedFilters']),
    options() {
      return this.getAllOptions[this.filterKey]
          ? this.getAllOptions[this.filterKey]
          : [];
    },
    // Список чекнутых опций
    checkList() {
      return this.getCheckedFilters[this.filterKey]
          ? this.getCheckedFilters[this.filterKey]
          : [];
    }
  },
  watch: {
    getAllOptions() {
      this.isInit = true;
    }
  },
  updated() {
    if (this.isInit) {
      this.isInit = false;
      this.init();
    }
    if (this.isBigAccordHide && this.bigAccordHeight !== `0`) {
      setTimeout(() => {
        this.bigAccordHeight = `0`;
      }, 0);
    }
  }
}
</script>

<style lang="scss">
@import '~@/assets/style/variables.scss';

