  padding: 20px 0 20px;
  border-bottom: 1px solid $white-dark;

  &__heading {
    justify-content: space-between;
    display: flex;
    margin: 0 0 20px;
  }

  &__title {
    font-family: $helvetica;
    font-size: 12px;
    line-height: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: $black-medium;
  }

  &__mark {
    position: relative;
    width: 10px;
    height: 10px;
    padding: 0;
    border: 0;
    outline: none;
    background-color: $white;
    margin: 2px 0 0;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: $black-medium;
    }

    &::before {
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 2px;
    }

    &::after {
      top: 50%;
      left: calc(50% - 1px);
      width: 2px;
      height: 0;
      transition-property: top, height;
      transition-duration: $animation-duration;
    }

    &--plus::after {
      top: 0;
      height: 100%;
    }

    &--footer {
      position: absolute;
      top: 5px;
      left: 0;
      display: block;
    }
  }

  &__search {
    width: 200px;
    height: 29px;
    padding: 1px 10px 2px;
    font-family: $helvetica;
    font-size: 12px;
    line-height: 26px;
    color: $black-medium;
    border: 1px solid $white-dark;
    background-color: $white;
    transition-property: border-color;
    transition-duration: $animation-duration;
    margin: 0 0 10px 13px;

    &::placeholder {
      color: $white-dark;
    }

    &:focus {
      border-color: $black-medium;
      outline: 0;
    }
  }

  &__accordion {
    transition-property: height;
    transition-duration: $animation-duration;
    margin: 0 0 0 -13px;
    overflow: hidden;
  }

  &__list {
    list-style: none;
    padding: 0 0 0 13px;
    transition-property: height;
    transition-duration: $animation-duration;
    margin: 0;
    overflow: hidden;
  }

  &__item {
    position: relative;
    display: flex;
    font-family: $helvetica;
    font-size: 12px;
    line-height: 26px;
    color: $black-light;
    margin: 0 0 6px;

    &--thin {
      margin: 0 0 1px;
    }
  }

  &__option {
    padding: 0 20px 0 0;
    font-size: inherit;
    line-height: inherit;
    border: 0;
    outline: 0;
    background-color: $white;
    cursor: pointer;
  }

  &__checkbox {
    display: inline-block;
    vertical-align: top;
    width: 16px;
