    &--checked {
      opacity: 1;
    }
  }

  &__clear {
    position: absolute;
    top: 9px;
    left: -12px;
    display: block;
    width: 7px;
    height: 7px;
    opacity: 0;
    background-color: $white;
    transition-property: transform, opacity;
    transition-duration: $animation-duration;
    transition-timing-function: ease;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: $black-light;
    }

    &::before {
      top: 3px;
      left: 0;
      width: 7px;
      height: 1px;
    }

    &::after {
      top: 0;
      left: 3px;
      width: 1px;
      height: 7px;
    }

    .filter__option:hover & {
      opacity: 1;
    }

    &--checked {
      opacity: 1;
      transform: rotate(45deg);
    }
  }

  &__number {
    color: $grey-light;
    margin: 0 0 0 auto;
  }

  &__footer {
    position: relative;
    padding: 0 15px 0 20px;
    font-family: $helvetica;
    font-size: 12px;
    line-height: 26px;
