<template>
  <div class="showroom">
    <div class="showroom__header">
      <div class="showroom__filters" @click.capture="queueClickHandler">
        <button v-for="item in getOptionsQueue" :data-key="item.key" :data-value="item.value"
                class="showroom__filter">
          <span class="showroom__legend">{{ item.value }}</span>
          <svg class="showroom__icon-cross" fill="#202020" height="10" width="10"
               xmlns="http://www.w3.org/2000/svg">
            <path d="M7.12 2.16L2.08 7.02l.7.72 5.04-4.86-.7-.72z"/>
            <path d="M7.74 7.12L2.88 2.08l-.72.7 4.86 5.04.72-.7z"/>
          </svg>
        </button>
      </div>
      <div class="showroom__style">
        <input id="showroom-1" v-model="isPictureSmall" :value="false" checked
               class="visually-hidden" name="showroom"
               type="radio">
        <label class="showroom__tile" for="showroom-1">
          <svg height="15" width="15" xmlns="http://www.w3.org/2000/svg">
            <path d="M0 0h6v6H0zM9 0h6v6H9zM0 9h6v6H0zM9 9h6v6H9z"/>
          </svg>
        </label>
        <input id="showroom-2" v-model="isPictureSmall" :value="true" class="visually-hidden"
               name="showroom"
               type="radio">
        <label class="showroom__tile" for="showroom-2">
          <svg height="15" width="15" xmlns="http://www.w3.org/2000/svg">
            <path clip-rule="evenodd"
                  d="M0 0h2.6v2.6H0V0zm0 6.2h2.6v2.6H0V6.2zm0 6.2h2.6V15H0v-2.6zM6.2 0h2.6v2.6H6.2V0zm0 6.2h2.6v2.6H6.2V6.2zm0 6.2h2.6V15H6.2v-2.6zM12.4 0H15v2.6h-2.6V0zm0 6.2H15v2.6h-2.6V6.2zm0 6.2H15V15h-2.6v-2.6z"
                  fill-rule="evenodd"/>
          </svg>
        </label>
      </div>
    </div>
    <transition-group :class="{'showroom__pictures--small': isPictureSmall}"
                      class="showroom__pictures"
                      name="showroom__picture" tag="ul">
      <li v-for="(picture, id) in getShowedPictures" v-if="id >= startId && id <= endId"
          :key="uniqNumber + id"
          class="showroom__picture">
        <div class="showroom__img-wrapper">
          <img :alt="picture.name" :src="picture.img" class="showroom__img">
        </div>
        <p class="showroom__caption"><span v-html="picture.name"></span><br>{{ picture.year }}</p>
      </li>
    </transition-group>
    <div class="showroom__pagination" @click.capture="pageClickHandler">
      <button v-for="page in lastPage"
              :class="{'showroom__page-number--active': page === currentPage}"
              class="showroom__page-number" type="button">
        {{ page }}
      </button>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: "Showroom",
  data: function () {
    return {
      isPictureSmall: false,
      uniqNumber: 0,
      itemsOnPage: {
        largePictures: 10,
        smallPictures: 15
      },
      startId: 0,
      endId: 0,
      currentPage: 1,
      lastPage: 0,
    }
  },
  methods: {
    ...mapActions(['toggleOptions']),
    queueClickHandler(evt) {
      this.toggleOptions([
        evt.target.dataset.key,
        evt.target.dataset.value
      ]);
    },
    countPage() {
      this.lastPage = Math.ceil(this.getShowedPictures.length
          / (this.isPictureSmall ? this.itemsOnPage.smallPictures : this.itemsOnPage.largePictures));
    },
    showPage(page) {
      const itemsOnPage = this.isPictureSmall ? this.itemsOnPage.smallPictures : this.itemsOnPage.largePictures;
      this.startId = itemsOnPage * (page - 1);
      this.endId = (itemsOnPage * page) - 1;
    },
    pageClickHandler(evt) {
      if (evt.target.tagName.toLowerCase() === 'button') {
        evt.target.blur();
        this.currentPage = evt.target.innerText * 1;
        this.showPage(this.currentPage);
      }
    }
  },
  computed: mapGetters(['getOptionsQueue', 'getShowedPictures']),
  watch: {
    isPictureSmall() {
      // Пересчет текущей страницы при перестроении сетки
      if (this.isPictureSmall) {
        this.currentPage = Math.ceil(this.currentPage * this.itemsOnPage.largePictures / this.itemsOnPage.smallPictures);
      } else {
        this.currentPage = Math.floor(this.currentPage * this.itemsOnPage.smallPictures / this.itemsOnPage.largePictures);
      }

      this.countPage();
      this.showPage(this.currentPage)
    },
    getShowedPictures() {
      this.uniqNumber = Date.now();
      this.currentPage = 1;
      this.countPage();
      this.showPage(this.currentPage);
    }
  }
}
</script>

<style lang="scss">
