    font-family: $helvetica;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: $black-light;
    margin: 0;
    overflow: hidden;

    @include mq(1023) {
      justify-content: flex-start;
      padding: 0;
    }

    @include mq(767) {
      justify-content: space-between;
      margin: 0 20px;
    }

    &--small {
      padding: 0;
    }
  }

  &__picture {
    flex-shrink: 0;
    width: 280px;
    opacity: 1;
    transition-property: opacity;
    transition-duration: $animation-duration;
    margin: 0 0 24px 40px;

    @include mq(1023) {
      width: 40%;
    }

    @include mq(767) {
      width: percentage(120 / 280);
      margin: 0 0 24px;
    }

    .showroom__pictures--small & {
      width: 200px;
    }

    &-enter-active {
      position: absolute;
