<template>
  <div class="filter-years">
    <FilterHead :is-plus="isHide" :title="title" @toggleView="toggleAccordion"/>
    <div ref="accordion" :style="{height: accordHeight}" class="filter-years__accordion">
      <div class="filter-years__search">
        <input ref="inputMin" class="filter-years__input" placeholder="с" type="text"
               @input="inputHandler('rangeMin', $event)">
        <input ref="inputMax" class="filter-years__input" placeholder="до" type="text"
               @input="inputHandler('rangeMax', $event)">
      </div>
      <ul class="filter-years__list">
        <li v-for="(item, id) in presetPeriod" class="filter-years__item">
          <button class="filter-years__option" @click="optionClickHandler(id)">
            <span class="filter-years__checkbox">
              <svg :class="{'filter-years__check--checked': id === checkedId}" class="filter__check"
                   height="8" width="10" xmlns="http://www.w3.org/2000/svg">
                <path d="M9 1L4.1 6 1 2.85" stroke="#4B4B4B" stroke-miterlimit="10"
                      stroke-width="2"/>
              </svg>
            </span>
            {{ item.text }}
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import FilterHead from "./Filter-head";
import {mapActions, mapGetters} from "vuex";

export default {
  name: "Filter-range",
  components: {FilterHead},
  props: {
    // Имя свойства в элементе массива исходных данных по значению которого будет производиться фильтраця
    filtersName: String,
    // Наименование блока отображаемое на экране
    title: String
  },
  data: () => {
    return {
      // Состояние блока, true свернут
      isHide: false,
      // Значение height элемента .filter__accordion
      accordHeight: 'auto',
      rangeMin: 0,
      rangeMax: Infinity,
      presetPeriod: [
        {text: 'до 1900', min: 0, max: 1900},
        {text: '1901-1916', min: 1901, max: 1916},
        {text: '1917 и позже', min: 1917, max: Infinity}
      ]
    }
  },
  computed: {
    ...mapGetters(['getCheckedFilters']),
    checkedId() {
      if (this.getCheckedFilters && this.getCheckedFilters[this.filtersName]) {
        return this.getCheckedFilters[this.filtersName].checkedId;
      } else {
        return -1;
      }
    }
  },
  methods: {
    ...mapActions(['changeRange']),
    // Метод переключает состояние аккордиона
    toggleAccordion() {
      this.isHide = !this.isHide;
      this.accordHeight = this.isHide
          ? `0`
          : `${this.$refs.accordion.scrollHeight}px`;
    },
    optionClickHandler(newId) {
      const isNotChecked = this.checkedId !== newId;
      this.changeRange({
        checkedId: isNotChecked ? newId : -1,
        key: this.filtersName,
        min: isNotChecked ? this.presetPeriod[newId].min : 0,
        max: isNotChecked ? this.presetPeriod[newId].max : Infinity
      });
      this.$refs.inputMin.value = ``;
      this.$refs.inputMax.value = ``;
      this.rangeMin = 0;
      this.rangeMax = Infinity;
    },
    inputHandler(name, evt) {
      const value = evt.target.value.replace(/\D/g, ``);
      if (value.length === 4 && this[name] !== value * 1) {
        this[name] = value * 1;
        this.changeRange({
          key: this.filtersName,
          checkedId: -1,
          min: this.rangeMin,
          max: this.rangeMax
        });
      } else if (value.length > 4) {
        evt.target.value = value.slice(0, 4);
      }
    }
  },
  mounted() {
    this.accordHeight = `${this.$refs.accordion.scrollHeight}px`;
  }
}
</script>

<style lang="scss">
@import '~@/assets/style/variables.scss';

.filter-years {
  padding: 20px 0 20px;
  font-family: $helvetica;
  font-size: 12px;
  line-height: 22px;

  &__accordion {
    transition-property: height;
    transition-duration: $animation-duration;
    overflow: hidden;
  }

  &__search {
    color: $black-light;
    margin: 0 0 14px;
  }

  &__input {
    width: 80px;
    height: 29px;
    padding: 4px 10px 3px;
    border: 1px solid $white-dark;
    margin: 0 10px 0 0;

    &::placeholder {
      color: $white-dark;
    }
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__item {
    margin: 0 0 5px;
  }

  &__option {
    padding: 0;
    color: $black-light;
    border: 0;
    outline: 0;
    background-color: $white;
    cursor: pointer;
  }

  &__checkbox {
    display: inline-block;
    vertical-align: top;
    width: 16px;
    height: 16px;
    padding: 3px 2px;
    border: 1px solid $white-dark;
    border-radius: 4px;
    margin: -1px 6px 0 0;
  }

  &__check {
    display: block;
    opacity: 0;
    fill: none;
    transition-property: opacity;
    transition-duration: $animation-duration;

    &--checked {
      opacity: 1;
    }
  }
}
</style>
