<template>
  <div class="filters" :class="{'filters--hidden': isHidden}">
    <FilterUnit :is-checkbox-model="false" filter-key="work" title="работы"
                view-toggle-capture="Все работы"/>
    <FilterUnit :is-checkbox-model="true" :with-search="true" filter-key="plot"
                title="сюжет"
                view-toggle-capture="Все сюжеты"/>
    <FilterUnit :is-checkbox-model="true" filter-key="style" title="стили"
                view-toggle-capture="Все стили"/>
    <FilterUnit :is-checkbox-model="true" :with-search="true" filter-key="technics"
                title="техника"
                view-toggle-capture="Все техники"/>
    <FilterRange filters-name="year" title="период"/>
  </div>
</template>

<script>
import {mapActions} from "vuex";
import FilterUnit from "./Filter-unit";
import FilterRange from "./Filter-range";

// Массив ключей по которым будет осуществлятся фильтрация
const OPTION_FILTERS = ['work', 'plot', 'style', 'technics'];
const RANGE_FILTER = 'year'

export default {
  name: "Filters",
  props: { isHidden: Boolean },
  methods: mapActions(['initFilterKeys']),
  created() {
    this.initFilterKeys({optionFilters: OPTION_FILTERS, rangeFilter: RANGE_FILTER});
  },
  components: {FilterRange, FilterUnit}
}
</script>

