<template>
  <div class="filter-head">
    <span class="filter-head__title">{{ title }}</span>
    <button :class="{ 'filter-head__mark--plus': isPlus }" class="filter-head__mark" type="button"
            @click="$emit(`toggleView`)"></button>
  </div>
</template>

<script>
export default {
  name: "Filter-head",
  props: {isPlus: Boolean, title: String},
}
</script>

<style lang="scss">
@import '~@/assets/style/variables.scss';
@import '~@/assets/style/mixin.scss';

.filter-head {
  justify-content: space-between;
  display: flex;
  margin: 0 0 20px;

  @include mq(767) {
    justify-content: flex-start;
  }


  &__title {
    font-family: $helvetica;
    font-size: 12px;
    line-height: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: $black-medium;
  }

  &__mark {
    position: relative;
    width: 10px;
    height: 10px;
    padding: 0;
    border: 0;
    outline: none;
    background-color: $white;
    margin: 2px 0 0;
    cursor: pointer;

    @include mq(767) {
      margin: 2px 0 0 20px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: $black-medium;
    }

    &::before {
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 2px;
    }

    &::after {
      top: 50%;
      left: calc(50% - 1px);
      width: 2px;
      height: 0;
      transition-property: top, height;
      transition-duration: $animation-duration;
    }

    &--plus::after {
      top: 0;
      height: 100%;
    }
  }
}

</style>
