<template>
  <ul class="tabs" @click.capture="clickHandler">
    <li v-for="(it, id) in tabList" :key="id"
        :class="{'tabs__item--active': id === idActive}" :data-id="id"
        class="tabs__item">
      {{ it.tabName }}
    </li>
  </ul>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: "Tabs",
  props: { tabList: Array },
  data: () => {
    return {
      idActive: 0
    }
  },
  methods: {
    ...mapActions(['fetchData']),
    clickHandler: function (evt) {
      if (evt.target.dataset.id) {
        this.idActive = evt.target.dataset.id * 1;
        this.fetchData(this.tabList[this.idActive].url);
      }
    }
  },
  created() {
    this.fetchData(this.tabList[0].url);
  }
}
</script>

